
.container{
    display: flex;
    flex-direction: row;
  }

.calcHistory{
    height: 560px;
    width: 0px;
    align-content: center;
    padding: 10px;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    color: white;
    background-color: #4357692d;
    background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: all 0.5s;
    
}

.calcHistory.show{
    width: 240px;
    visibility: visible;
    overflow-y: scroll;
    opacity: 100;

}


.historyList{
  list-style-type: none;
  padding-left: 0px;
  padding-right: 0px;
  padding-top: 10px;
    
}



